:root{
    --varWidth: 100% !important;
}
.choose__image__preview{
    width: var(--varWidth);
    height: calc(var(--varWidth) * 9 / 16);
}
.choose__image__preview img{
    object-fit: contain !important;
}
.select__filter{
    border: none !important;
    background-color: transparent !important;
    appearance: none !important;
    outline: none !important;
}
.new__film{
position: absolute;
top:20px;
right: 20px;
border-radius: 50%;
color: red;
}
.td__movie__preview{
    position: relative;
}