.container-pay {
    margin-top: 100px;
}

.title-vip {
    color: black;
    font-weight: 600;
    text-align: center;
}

.des-vip {
    text-align: center;
}

.goiVip {
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    border-radius: 5px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.masterCard {
    border: 0.5px solid gray;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;

    span {
        font-size: 0.8rem;
    }
}

.infoVip__item {
    display: flex;
    justify-content: space-between;
}

.infoVIp {
    display: flex;
    align-items: flex-start;
    
    width: 100%;

    img {
        max-width: 125px;
        height: auto !important;
        margin-right: 0.7rem;
    }

    .infoVip__body {
        flex: 1;
        .infoVip__item {
            display: flex;
            justify-content: space-between;
            padding: 7px 0px;

            span {
                color: black;
            }
        }
    }
}

.infoVip__title {
    display: flex;
    justify-content: space-between;
}
.payQR__body{
    display: flex;
    .payQR__image{
        max-height: 190px;
        img{
            object-fit: contain !important;
        }

    }
    .payQR__item{
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
    }
}